	module.exports = {"data":{"foundationYaml":{"title":"Typography","category":"Foundation","examples":[{"title":null,"introduction":null,"subTitle":null,"css":null,"html":"<h1>Heading 1, bold 42</h1>\n<h2 class=\"text-bhp-orange-1\">Heading 2, bold 32</h2>\n<h3>Heading 3, bold 30</h3>\n<h4>Heading 4, bold 22</h4>\n<h5>HEADING 5, <span class=\"text-lowercase\">bold 14 uppercase</span></h5>\n","actualHtml":"<div class=\"p-3\">\n  <h1>Heading 1, bold 42</h1>\n  <h2 class=\"text-bhp-orange-1\">Heading 2, bold 32</h2>\n  <h3>Heading 3, bold 30</h3>\n  <h4>Heading 4, bold 22</h4>\n  <h5>HEADING 5, <span class=\"text-lowercase\">bold 14 uppercase</span></h5>\n</div>\n"},{"title":null,"introduction":null,"subTitle":null,"css":null,"html":"<p class=\"lead\">\n  Body text large / <a href=\"https://www.bhp.com\">Link text large</a><br />\n</p>\n","actualHtml":"<p class=\"lead\">\n  Body text large / <a href=\"https://www.bhp.com\">Link text large</a><br />\n</p>\n"},{"title":null,"introduction":null,"subTitle":null,"css":null,"html":"<p>\n  Body text / <a href=\"https://www.bhp.com\">link text</a>\n  - this is the default text size - 13px/18px #333333<br />\n  Note: Body text used in tables can be reduced to 11px in some cases.\n</p>\n","actualHtml":null},{"title":null,"introduction":null,"subTitle":null,"css":null,"html":"<p class=\"small\">\n  Small text - this is the default text size for notes, legal texts...\n  - 11px/15px #979797 @50% opacity<br />\n  Lorem ipsum dolor sit amet, quando quodsi electram ei has, ne alii inermis\n  neglegentur sea.\n  <strong>An sea quodsi mentitum vivendum, ferri sadipscing pri ne.</strong>\n</p>\n","actualHtml":null},{"title":"Text colours","introduction":"Texts will generally use four colour variations:","subTitle":null,"css":null,"html":"<div class=\"container\">\n  <div class=\"row\">\n    <div class=\"col-md-2 p-2\">Default colour</div>\n    <div class=\"col-md-2 p-2\">#333</div>\n  </div>\n  <div class=\"row text-bhp-orange-1\">\n    <div class=\"col-md-2 p-2\">Primary colour</div>\n    <div class=\"col-md-2 p-2\">#dd5900</div>\n  </div>\n  <div class=\"row text-white\">\n    <div class=\"col-md-2 p-2 bg-bhp-orange-1\">Negative colour</div>\n    <div class=\"col-md-2 p-2 bg-bhp-orange-1\">#fff</div>\n  </div>\n  <div class=\"row text-muted\">\n    <div class=\"col-md-2 p-2\">Notes/Disabled colour</div>\n    <div class=\"col-md-2 p-2\">#333 @50% opacity</div>\n  </div>\n</div>\n","actualHtml":null}],"fields":{"slug":"/foundation/typography/","preHtml":"<p>Arial is BHP's everyday font. As a system font, Arial ensures consistency when distributing files across workstations and platforms. Arial has been installed as our default across all desktop computers. If you require further details regarding this font for your agency/provider please contact your local Corporate Affairs team or email Corporate Brand.</p>\n<p>The font Graphik Web is accepted as the secondary option for some designs. Its use and styling similarly follows the primary font, Arial. Typical use would be for emphasis, special design or captions.</p>\n<p>The default body text is 13px. For optimum reading length, the paragraph-width is capped at 694px.</p>\n<p>Tips: Too many type sizes and styles at once can disturb any layout. A typographic scale has a limited set of type sizes that work well together along with the layout grid. Avoid italic text as it impedes the accessibility of your design.</p>\n<p>White text on dark backgrounds should have an opacity of 100%.</p>\n","postHtml":"\n"}}},"pathContext":{"slug":"/foundation/typography/"}}

